body {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: url(https://dev-static.hotelsforhope.com/ares/site_configs/cfp_houston_internal_24-92253/img/banner.png) no-repeat top center;
  font-family: "Graphik-Regular";
  background-attachment: fixed;
  background-size: 100% auto;
  overflow-x: hidden;
}

header {
  border-bottom: unset;
  height: 115px;
  justify-content: space-between;
}

header a {
  background: url(https://dev-static.hotelsforhope.com/ares/site_configs/cfp_houston_internal_24-92253/img/logo_bg.svg) top left/117% 150% no-repeat;
}

a.logo {
  width: 40%;
}

.logo img {
  margin-top: 40px;
  padding: 5px;
}

.ArnSearchContainerMainDiv,
.PropDetailView,
#theBookingPage {
  border: 1px solid #e5e5e5;
  width: 96vw;
  background-color: #fff;
  margin: 84px 0 5px 0;
}

.RootBody .ArnSearchContainerMainDiv {
  background-color: unset;
  border: unset;
}

.SinglePropDetail .ArnSubPage,
#theBookingPage {
  max-width: unset;
  padding: 0 40px;
}

span.exclusive-rate {
  position: absolute;
  border: unset;
  margin: 0;
  top: unset;
  left: unset;
  width: 100%;
  text-align: center;
  padding: 10px;
  font-family: "UnitedSansReg-Heavy";
}

.ArnPrimarySearchContainer,
.SimpleSearch {
  background-color: transparent;
  color: #000;
}

.ArnSupportLinks {
  background: #fff;
}

.ArnSupportLinks .dvd {
  color: #000;
}

.ArnSupportLinks a {
  color: #000;
}

.pb-container img {
  filter: invert(0.5);
}

.SinglePropDetail .PropertyAmenities li {
  padding: 3px 0;
}

.SinglePropDetail .ArnNightlyRate strong div:nth-last-child(2) {
  padding: 7px 0;
}

.ArnSubPage {
  margin: 65px auto;
}

.WBRateGuaranteeForm2 .zsFormClass {
  background: transparent;
}

.SearchHotels .ArnSearchHeader {
  padding: 35px 12px 12px 12px;
}

.SearchHotels .ArnSecondarySearchOuterContainer {
  padding: 0 8px;
}

.sort-wrapper h4 {
  margin-top: 12px;
}

.SinglePropDetail .PropDetailView {
  margin: 84px 0 5px 0;
}

@media screen and (max-width: 1100px) {
  .ArnSearchContainerMainDiv,
.PropDetailView,
#theBookingPage {
    width: unset;
  }

  a.logo {
    width: 40%;
  }

  .ArnSubPage {
    margin: 0 auto;
  }

  .SinglePropDetail .ArnPropName {
    padding: 15px 0;
  }

  .SearchHotels .ArnPropertyMapInner {
    width: 100% !important;
    height: 100vh !important;
  }
}
.event-location {
  display: flex;
  flex-direction: column;
  margin-right: 15px;
}

.event-location a {
  color: #c9920e;
  background: #000;
  border: 2px solid #c9920e;
  font-family: "UnitedSansReg-Heavy";
  text-transform: uppercase;
  letter-spacing: 1px;
  text-decoration: none;
  font-size: 17px;
  padding: 4px;
  margin: 3px 0;
